export const login = (form, handleSuccess) => {
  return {
    type: 'LOGIN',
    payload: new Promise((resolve, reject) => {
      axios
        .post(`${process.env.REACT_APP_BACKEND_URL}/login`, form)
        .then((response) => {
          handleSuccess(response.data);
          resolve(response.data);
        })
        .catch((err) => {
          reject(err);
        });
    }),
  };
};

