export const getDetailUser = (id) => {
  return {
    type: 'GET_DETAIL_USER',
    payload: new Promise((resolve, reject) => {
      axios
        .get(`${process.env.REACT_APP_BACKEND_URL}/user/${id}`)
        .then((response) => {
          // handleSuccess(response.data);
          resolve(response.data);
        })
        .catch((err) => {
          reject(err);
        });
    }),
  };
};
